import swh from '@/assets/sw_helpers.js'

class StellariumController {
  constructor (stelInstance) {
    this.stel = stelInstance
    this._lastSelectionId = null
    this._observerTimer = null
  }

  async selectAndTrackObjectByName (name, zoomFovDeg = 10) {
    try {
      const source = await swh.lookupSkySourceByName(name)
      if (!source) {
        console.warn(`[StellariumController] SkySource not found for: ${name}`)
        return
      }

      let sweObj = swh.skySource2SweObj(source)
      if (!sweObj) {
        sweObj = this.stel.createObj(source.model, source)
      }

      if (!sweObj) {
        console.warn(`[StellariumController] Cannot create/find SWEObject for ${name}`)
        return
      }
      this.stel.core.selection = sweObj
      this.stel.pointAndLock(sweObj, zoomFovDeg * Math.PI / 180)

      if (window.AndroidBridge?.postMessage) {
        window.AndroidBridge.postMessage(JSON.stringify({
          type: 'object_selected_by_name',
          payload: {
            name: source.names?.[1] || source.names?.[0] || name,
            types: source.types || [],
            hip: source.hip || null,
            radec: source.radec || null
          }
        }))
      }

      console.log(`[StellariumController] ✅ '${name}' selected and locked`)
    } catch (err) {
      console.error('[StellariumController] selectAndTrackObjectByName error:', err)
    }
  }

  // 🪐 Selection 감시 시작
  startSelectionObserver (interval = 200) {
    if (this._observerTimer) clearInterval(this._observerTimer)

    this._observerTimer = setInterval(() => {
      const currentSelection = this.stel.core.selection
      if (!currentSelection) {
        // 선택 해제된 경우 처리
        if (this._lastSelectionId !== null) {
          this._lastSelectionId = null
          if (window.AndroidBridge?.postMessage) {
            window.AndroidBridge.postMessage(
              JSON.stringify({ type: 'object_unselected' })
            )
          }
        }
        return
      }

      // SWEObject의 포인터 값으로 선택 상태 변경 체크
      const currentId = currentSelection.ptr
      if (this._lastSelectionId !== currentId) {
        this._lastSelectionId = currentId

        // 🌟 SkySource로 변환 후 Android로 전달
        swh.sweObj2SkySource(currentSelection).then((skySource) => {
          const payload = {
            name: skySource.names?.[1] || skySource.names?.[0] || 'Unknown',
            type: skySource.types?.[0] || '',
            radec: skySource.radec || null,
            hip: skySource.hip || null,
            model_data: skySource.model_data || {}
          }

          if (window.AndroidBridge?.postMessage) {
            window.AndroidBridge.postMessage(
              JSON.stringify({
                type: 'object_selected',
                payload
              })
            )
          }
        }).catch(err => console.warn('[StellariumController] sweObj2SkySource failed:', err))
      }
    }, interval)
  }

  // 🛑 감시 중단
  stopSelectionObserver () {
    if (this._observerTimer) {
      clearInterval(this._observerTimer)
      this._observerTimer = null
    }
  }

  // 🌌 별자리 토글
  toggleConstellations (visible) {
    this.stel.core.constellations.lines_visible = visible
  }

  // 🕸️ 적도 격자선 (J2000)
  toggleEquatorialGrid (visible) {
    this.stel.core.lines.equatorial.visible = visible
  }

  // 🕸️ 적도 격자선 (JNow)
  toggleEquatorialJNowGrid (visible) {
    this.stel.core.lines.equatorial_jnow.visible = visible
  }

  // 🧭 방위선 격자
  toggleAzimuthalGrid (visible) {
    this.stel.core.lines.azimuthal.visible = visible
  }

  // ☁️ 대기 / 지형
  toggleAtmosphere (visible) {
    this.stel.core.atmosphere.visible = visible
  }

  toggleLandscape (visible) {
    this.stel.core.landscapes.visible = visible
  }

  // 🪐 심우주 객체(DSOs)
  toggleDSOs (visible) {
    this.stel.core.dsos.visible = visible
  }

  // 🕓 시간 변경
  setTime (date) {
    this.stel.core.observer.utc = date.getMJD()
  }

  // 📍 위치 변경
  setLocation (lat, lng, alt = 0) {
    this.stel.core.observer.latitude = lat * Math.PI / 180
    this.stel.core.observer.longitude = lng * Math.PI / 180
    this.stel.core.observer.elevation = alt
  }

  // 🌓 시야각 변경 (줌 인/아웃)
  setFov (deg) {
    this.stel.core.fov = Math.min(deg * Math.PI / 180, 180)
  }

  // 🧭 시점 이동
  setViewDirection (yawDeg, pitchDeg) {
    if (!this.stel || !this.stel.core) {
      return console.warn('[⚠️] 엔진이 준비되지 않았습니다.')
    }
    this.stel.core.observer.yaw = yawDeg * Math.PI / 180
    this.stel.core.observer.pitch = pitchDeg * Math.PI / 180
  }
}

export default StellariumController
